body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.thumbnail {
  max-height: 750px;
  height: 750px;
}

#overview {
  display: block; /* Fallback for non-webkit */
  display: -webkit-box;
  max-width: 100%;
  height: 60px; /* Fallback for non-webkit */
  -webkit-line-clamp: 3;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.jumbotron {
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right bottom, from(black), to(#337ab7));
  background-image: -webkit-linear-gradient(top left, black, #337ab7);
  background-image: -o-linear-gradient(top left, black, #337ab7);
  background-image: linear-gradient(to bottom right, black, #337ab7);
}

.form-inline {
  margin-bottom: 2em;
}

.form-group {
  -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
          transform: scale(1.5);
}

.favourites-button {
  position: fixed;
  left: 15px;
  bottom: 100px;
}


